<template>
<div id="app">
    <!-- 内容 -->
    <el-container direction="vertical">
        <!-- 选项卡 -->
        <bk-tab :active-name="tabName" @tab-changed="changeTabPanel">
            <!-- 实例选项卡 -->
            <bk-tabpanel name="instance" :title="i18n.instance">
                <div class="p20">{{i18n.instanceName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="plotlyDataByCategory()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="plotlyDataByCategory()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->
                            </el-col>
                        </div>
                        <!-- plotly图表 -->
                        <el-col :span="22">
                            <div class="block analysis-plotly-class" id="instanceBar" v-if="instanceShow"></div>
                            <NoData class="empty-data" :style="{height: noDataHeight}" v-else/>
                        </el-col>
                        <!-- plotly图表end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 实例选项卡end -->

            <!-- 占比选项卡 -->
            <bk-tabpanel name="prop" :title="i18n.prop">
                <div class="p20">{{i18n.propName}}</div>
                <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="block">
                        <el-col :span="23" :offset="1">
                            <!-- 时间选择器 -->
                            <el-col :span="8" class="analysis-date-picker">
                                {{i18n.startAndEndTime}}
                                <el-date-picker :clearable="false" v-model="time" @change="plotlyDataByBizCCId()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                </el-date-picker>
                            </el-col>
                            <!-- 时间选择器end -->

                            <!-- 分组选择器 -->
                            <el-col :span="5" class="analysis-select">
                                {{i18n.category}}
                                <el-select clearable filterable :placeholder="i18n.choice" @change="plotlyDataByBizCCId()" v-model="category">
                                    <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <!-- 分组选择器end -->
                        </el-col>
                    </div>
                     <!-- plotly图表 -->
                    <el-col :span="22">
                        <div class="block analysis-plotly-class" id="propBar" v-if="propShow" ></div>
                        <NoData class="empty-data" :style="{height: noDataHeight}" v-else/>
                    </el-col>
                    <!-- plotly图表end -->
                </div>
                </el-col>
            </bk-tabpanel>
            <!-- 占比选项卡end -->

            <!-- 详情选项卡 -->
            <bk-tabpanel name="node" :title="i18n.node">
                <div class="p20">{{i18n.nodeName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByNode()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByNode()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->

                                <!-- 分组选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.category}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByNode()" v-model="category">
                                        <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                 <!-- 分组选择器end -->
                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="nodeData"
                            :total="nodeTotal"
                            :otherHeight="otherHeight"
                            :pagination="nodePagination"
                            :options="dataTableOptions"
                            :columns="nodeColumns"
                            :operates="nodeOperates"
                            @handleSortChange="nodeHandleSortChange"
                            @handleSizeChange="nodeHandleSizeChange"
                            @handleIndexChange="nodeHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 详情选项卡end -->

            <!-- 耗时选项卡 -->
            <bk-tabpanel name="details" :title="i18n.details">
                <div class="p20">{{i18n.detailsName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByDetails()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByDetails()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->
                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="detailsData"
                            :total="detailsTotal"
                            :otherHeight="otherHeight"
                            :pagination="detailsPagination"
                            :options="dataTableOptions"
                            :columns="detailsColumns"
                            :operates="detailsOperates"
                            @handleSortChange="detailsHandleSortChange"
                            @handleSizeChange="detailsHandleSizeChange"
                            @handleIndexChange="detailsHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 耗时选项卡end -->

        </bk-tab>
        <!-- 选项卡end -->
    </el-container>
    <!-- 内容end -->
</div>
</template>

<script>
import '@/utils/i18n.js'
import {mapActions, mapState} from 'vuex'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import NoData from '@/components/common/base/NoData.vue'
import {AnalysisMixins} from '@/assets/js/analysisMixins.js'

export default {
    name: 'AnalysisInstance',
    components: {
        NoData,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                startAndEndTime: gettext('任务创建时间：'),
                choice: gettext('请选择'),
                category: gettext('分类：'),
                businessName: gettext('业务：'),
                instanceName: gettext('任务分类统计数据'),
                propName: gettext('任务按业务维度统计数据'),
                node: gettext('任务详情'),
                details: gettext('执行耗时'),
                instance: gettext('任务分类'),
                prop: gettext('所属业务'),
                nodeName: gettext('各任务中实际执行的原子节点、子流程节点、网关节点个数'),
                detailsName: gettext('各任务执行耗时')
            },
            appType: 'app',
            time: [0, 0],
            category: '',
            biz_cc_id: '',
            nodeData: [],
            detailsData: [],
            otherHeight: 208, // 除了table表格之外的高度，为了做table表格的高度自适应
            nodeTotal: 0, // table数据总条数,
            nodePageIndex: 1, // 当前页码
            nodeLimit: 15, // 每页数量
            nodePagination: {
                // 分页操作
                limit: this.nodeLimit,
                pageIndex: this.nodePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            nodeColumns: [
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center', // 对其格式，可选（right，left，center）
                    minWidth: 150
                },
                {
                    prop: 'instanceName',
                    label: gettext('任务名称'),
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center',
                    width: 215
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center',
                    width: 130
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center',
                    width: 115
                },
                {
                    prop: 'atomTotal',
                    label: gettext('原子个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 155
                },
                {
                    prop: 'subprocessTotal',
                    label: gettext('子流程个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 190
                },
                {
                    prop: 'gatewaysTotal',
                    label: gettext('网关个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 170
                }
            ],
            nodeOperates: {
                width: 100, // 按钮大小
                // fixed: 'right', // 对齐方式
                flex: '0 0 100%', // 按钮间距
                isShow: true, // 是否显示操作按钮
                data: [
                    {
                        label: gettext('查看'), // 按钮显示文字
                        show: true, // 是否展示该按钮
                        plain: true, // 是否为朴素按钮
                        cls: 'bk-button bk-primary btn-size-mini',
                        method: (index, row) => {
                            // 回调方法
                            this.onInstanceHandleView(index, row)
                        }
                    }
                ]
            },
            detailsTotal: 0,
            detailsPageIndex: 1,
            detailsLimit: 15,
            detailsPagination: {
                // 分页操作
                limit: this.datailsLimit,
                pageIndex: this.datailsPageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            detailsColumns: [
                {
                    prop: 'businessName',
                    label: gettext('所属业务'),
                    align: 'center',
                    minWidth: 150
                },
                {
                    prop: 'instanceName',
                    label: gettext('任务名称'),
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center',
                    width: 115
                },
                {
                    prop: 'executeTime',
                    label: gettext('耗时（秒）'),
                    align: 'center',
                    sortable: 'custom',
                    minWidth: 145
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center',
                    width: 130
                }
            ],
            detailsOperates: {
                width: 100,
                flex: '0 0 100%',
                isShow: true,
                data: [
                    {
                        label: gettext('查看'),
                        show: true,
                        cls: 'bk-button bk-primary btn-size-mini',
                        method: (index, row) => {
                            // 回调方法
                            this.onInstanceHandleView(index, row)
                        }
                    }
                ]
            },
            instanceShow: true,
            propShow: true,
            noDataHeight: '600px',
            nodeOrderBy: '-instanceId',
            detailsOrderBy: '-instanceId',
            tabName: 'instance'
        }
    },
    created () {
        // 设置默认为30天前的查询
        let date = new Date()
        date.setHours(0, 0, 0)
        this.time[1] = Date.parse(date)
        date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
        this.time[0] = Date.parse(date)
    },
    mounted () {
        this.plotlyDataByCategory()
        if (this.categorys.length === 0) {
            this.getCategorys()
        }
        if (this.bizList.length === 0) {
            this.getBizList()
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        })
    },
    methods: {
        ...mapActions('task/', [
            'queryInstanceData'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        // 切换每页显示的数量
        nodeHandleSizeChange (limit) {
            this.nodePageIndex = 1
            this.nodeLimit = limit
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByNode()
        },
        // 切换页码
        nodeHandleIndexChange (pageIndex) {
            this.nodePageIndex = pageIndex
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByNode()
        },
        // 排序
        nodeHandleSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.nodeOrderBy = column[0].prop ?  order + column[0].prop : '-instanceId'
            this.dataTableDataByNode()
        },
        detailsHandleSizeChange (limit) {
            this.detailsPageIndex = 1
            this.detailsLimit = limit
            this.dataTableDataByDetails()
        },
        detailsHandleIndexChange (pageIndex) {
            this.detailsPageIndex = pageIndex
            this.dataTableDataByDetails()
        },
        detailsHandleSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.detailsOrderBy = column[0].prop ?  order + column[0].prop : '-instanceId'
            this.dataTableDataByDetails()
        },
        // 按钮点击事件
        onInstanceHandleView (index, row) {
            window.open(this.site_url + 'taskflow/execute/' + row.businessId + '/?instance_id=' + row.instanceId)
        },
        // 切换Tab事件
        changeTabPanel (name) {
            this.tabName = name
            switch (name) {
                case 'instance' :
                    this.plotlyDataByCategory()
                    break
                case 'prop' :
                    this.plotlyDataByBizCCId()
                    // 清除图表内容
                    this.clearPlot('instanceBar')
                    break
                case 'node' :
                    this.dataTableDataByNode()
                    break
                case 'details' :
                    this.dataTableDataByDetails()
                    break
            }
        },
        // 图表数据获取与使用
        async queryPlotlyData (data, id, title, seriesTitle) {
            const templateData = await this.queryInstanceData(data)
            let total = templateData.data.total
            // 显示Nodata界面
            if (id === 'propBar' && total === 0) {
                this.propShow = false
                return
            }
            // 显示plotly图表
            else if (id === 'propBar') {
                this.propShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                    var myPlot = document.getElementById(id)
                    var vueThis = this
                    myPlot.on('plotly_click', function (data) {
                        let point = data.points[0]
                        let id = point.data.id[point.pointIndex]
                        vueThis.tabName = 'node'
                        vueThis.biz_cc_id = id
                        vueThis.nodePageIndex = 1
                        vueThis.nodeLimit = 15
                        vueThis.dataTableDataByNode()
                    })
                })
            }
            else if (id === 'instanceBar' && total === 0) {
                this.instanceShow = false
                return
            }
            else if (id === 'instanceBar') {
                this.instanceShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                    var myPlot = document.getElementById(id)
                    var vueThis = this
                    myPlot.on('plotly_click', function (data) {
                        let point = data.points[0]
                        let id = point.data.id[point.pointIndex]
                        vueThis.tabName = 'node'
                        vueThis.category = id
                        vueThis.nodePageIndex = 1
                        vueThis.nodeLimit = 15
                        vueThis.dataTableDataByNode()
                    })
                })
            }
        },
        plotlyDataByCategory () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'category',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id
                    
                })
            }
            this.queryPlotlyData(data, 'instanceBar', gettext('任务个数和占比'))
        },
        plotlyDataByBizCCId () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'biz_cc_id',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    category: this.category
                })
            }
            this.queryPlotlyData(data, 'propBar', gettext('任务个数和占比'))
        },
        async queryDataTableData (data) {
            const templateData = await this.queryInstanceData(data)
            switch (data.group_by) {
                case 'instance_node':
                    this.nodeData = templateData.data.groups
                    this.nodeTotal = templateData.data.total
                    break
                case 'instance_details':
                    this.detailsData = templateData.data.groups
                    this.detailsTotal = templateData.data.total
                    break
            }
        },
        dataTableDataByNode () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'instance_node',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    order_by: this.nodeOrderBy
                }),
                pageIndex: this.nodePageIndex,
                limit: this.nodeLimit
            }
            this.queryDataTableData(data)
        },
        dataTableDataByDetails () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'instance_details',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    order_by: this.detailsOrderBy
                }),
                pageIndex: this.detailsPageIndex,
                limit: this.detailsLimit
            }
            this.queryDataTableData(data)
        }
    }
}
</script>
